/* 메인 컨테이너 */
.main-container {
  display: flex;
  min-height: calc(100vh - 64px);
  background-color: var(--bg-secondary);
}

/* 메인 컨텐츠 영역 */
.store-main-content {
  flex: 1;
  overflow-y: auto;
  transition: margin-left 0.3s ease;

  &.menu-open {
    margin-left: 0;
  }
}

.shopping-cart-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: var(--bg-secondary);
}

/* 헤더 제거 (app-store-layout-header 사용) */

/* 메인 컨텐츠 */
.cart-content {
  flex: 1;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 2rem;
}

/* 빈 장바구니 */
.empty-cart {
  text-align: center;
  padding: 4rem 2rem;
  background-color: var(--bg-primary);
  border-radius: 8px;
  box-shadow: 0 2px 4px var(--shadow);

  i {
    font-size: 4rem;
    color: var(--text-tertiary);
    margin-bottom: 1rem;
  }

  h2 {
    font-size: 1.5rem;
    color: var(--text-primary);
    margin-bottom: 0.5rem;
  }

  p {
    color: var(--text-secondary);
    margin-bottom: 2rem;
  }

  .btn-primary {
    padding: 0.75rem 2rem;
    font-size: 1rem;
  }
}

/* 장바구니 아이템 */
.cart-items {
  background-color: var(--bg-primary);
  border-radius: 8px;
  box-shadow: 0 2px 4px var(--shadow);
  overflow: hidden;
}

.cart-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1.5rem;
  border-bottom: 1px solid var(--border-color);
  background-color: var(--bg-secondary);

  .checkbox-label {
    display: flex;
    align-items: center;
    cursor: pointer;
    user-select: none;

    input[type="checkbox"] {
      margin-right: 0.5rem;
      cursor: pointer;
      width: 18px;
      height: 18px;
    }

    span {
      font-size: 0.95rem;
      color: var(--text-primary);
    }
  }

  .btn-text {
    background: none;
    border: none;
    color: var(--text-secondary);
    cursor: pointer;
    font-size: 0.9rem;
    padding: 0.5rem 1rem;
    border-radius: 4px;
    transition: all 0.2s;

    &:hover {
      background-color: var(--bg-tertiary);
      color: var(--text-primary);
    }
  }
}

.cart-item {
  display: grid;
  grid-template-columns: auto 100px 1fr auto;
  gap: 1rem;
  padding: 1.5rem;
  border-bottom: 1px solid var(--border-color);
  transition: background-color 0.2s;

  &:hover {
    background-color: var(--bg-secondary);
  }

  &:last-child {
    border-bottom: none;
  }

  .item-checkbox {
    display: flex;
    align-items: start;
    padding-top: 2rem;

    input[type="checkbox"] {
      cursor: pointer;
      width: 18px;
      height: 18px;
    }
  }

  .item-image {
    cursor: pointer;
    border-radius: 8px;
    overflow: hidden;
    background-color: var(--bg-secondary);
    width: 100px;
    height: 100px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: transform 0.2s;
    }

    &:hover img {
      transform: scale(1.05);
    }
  }

  .item-details {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;

    .item-store {
      font-size: 0.85rem;
      color: var(--text-secondary);
      cursor: pointer;
      transition: color 0.2s;

      &:hover {
        color: var(--text-primary);
        text-decoration: underline;
      }
    }

    .item-name {
      font-size: 1rem;
      font-weight: 500;
      color: var(--text-primary);
      cursor: pointer;
      transition: color 0.2s;

      &:hover {
        color: var(--accent-primary);
      }
    }

    .item-price-info {
      display: flex;
      flex-direction: column;
      gap: 0.25rem;

      .original-price {
        font-size: 0.85rem;
        color: var(--text-tertiary);
        text-decoration: line-through;
      }

      .discounted-price {
        display: flex;
        align-items: center;
        gap: 0.5rem;

        .price {
          font-size: 1.1rem;
          font-weight: 600;
          color: var(--text-primary);
        }

        .discount-badge {
          background-color: var(--error-color);
          color: white;
          padding: 0.125rem 0.5rem;
          border-radius: 4px;
          font-size: 0.75rem;
          font-weight: 600;
        }
      }

      .shipping-fee {
        font-size: 0.85rem;
        color: var(--text-secondary);
      }
    }

    .item-quantity {
      display: flex;
      align-items: center;
      gap: 0.5rem;
      margin-top: 0.5rem;

      .btn-quantity {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 32px;
        height: 32px;
        border: 1px solid var(--border-color);
        background-color: var(--bg-primary);
        color: var(--text-primary);
        border-radius: 4px;
        cursor: pointer;
        transition: all 0.2s;

        &:hover {
          background-color: var(--bg-secondary);
          border-color: var(--text-secondary);
        }

        i {
          font-size: 1rem;
          color: var(--text-secondary);
        }
      }

      .quantity-input {
        width: 60px;
        height: 32px;
        text-align: center;
        border: 1px solid var(--border-color);
        background-color: var(--bg-primary);
        color: var(--text-primary);
        border-radius: 4px;
        font-size: 0.95rem;
        font-weight: 500;

        &:focus {
          outline: none;
          border-color: var(--accent-primary);
        }
      }
    }

    .item-total {
      display: flex;
      align-items: center;
      gap: 0.5rem;
      margin-top: 0.5rem;
      padding-top: 0.5rem;
      border-top: 1px solid var(--border-color);

      .label {
        font-size: 0.9rem;
        color: var(--text-secondary);
      }

      .amount {
        font-size: 1.1rem;
        font-weight: 600;
        color: var(--accent-primary);
      }
    }
  }

  .btn-remove {
    background: none;
    border: none;
    cursor: pointer;
    padding: 0.5rem;
    border-radius: 50%;
    transition: background-color 0.2s;
    align-self: start;

    &:hover {
      background-color: var(--error-light);
    }

    i {
      font-size: 1.25rem;
      color: var(--text-secondary);
    }

    &:hover i {
      color: var(--error-color);
    }
  }
}

/* 하단 요약 & 결제 */
.cart-footer {
  background-color: var(--bg-primary);
  border-top: 1px solid var(--border-color);
  box-shadow: 0 -2px 4px var(--shadow);
  position: sticky;
  bottom: 0;
  z-index: 100;

  .footer-content {
    max-width: 1200px;
    margin: 0 auto;
    padding: 1.5rem 2rem;
  }

  .price-summary {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
    margin-bottom: 1.5rem;
    padding: 1rem;
    background-color: var(--bg-secondary);
    border-radius: 8px;

    .summary-row {
      display: flex;
      justify-content: space-between;
      font-size: 0.95rem;
      color: var(--text-secondary);

      &.discount {
        color: var(--error-color);
        font-weight: 500;
      }

      &.total {
        font-size: 1.25rem;
        font-weight: 600;
        color: var(--text-primary);
        padding-top: 0.75rem;
        border-top: 2px solid var(--border-color);
      }
    }
  }

  .footer-actions {
    display: flex;
    gap: 1rem;

    button {
      flex: 1;
      padding: 1rem;
      font-size: 1rem;
      font-weight: 600;
      border-radius: 8px;
      cursor: pointer;
      transition: all 0.2s;
      border: none;

      &:disabled {
        opacity: 0.5;
        cursor: not-allowed;
      }
    }

    .btn-secondary {
      background-color: var(--bg-primary);
      color: var(--text-primary);
      border: 1px solid var(--border-color);

      &:hover:not(:disabled) {
        background-color: var(--bg-secondary);
        border-color: var(--text-secondary);
      }
    }

    .btn-primary {
      background: var(--gradient-primary);
      color: #fff;
      border: none;

      &:hover:not(:disabled) {
        transform: translateY(-2px);
        box-shadow: 0 4px 8px var(--shadow);
      }
    }
  }
}

/* 반응형 */
@media (max-width: 768px) {
  .cart-header {
    padding: 1rem;

    .header-content h1 {
      font-size: 1.25rem;
    }
  }

  .cart-content {
    padding: 1rem;
  }

  .cart-item {
    grid-template-columns: auto 80px 1fr;
    gap: 0.75rem;

    .item-image {
      width: 80px;
      height: 80px;
    }

    .btn-remove {
      grid-column: 3;
      justify-self: end;
    }
  }

  .cart-footer {
    .footer-content {
      padding: 1rem;
    }

    .footer-actions {
      flex-direction: column;

      button {
        width: 100%;
      }
    }
  }
}
